import React from 'react';
import style from './main.module.scss';
import styleContainer from '../common/styles/container.module.css'
import {TextEffect} from "../common/texteffects/Effect";
import mainPhoto from "../assets/image/photo_2021-10-08_00-22-05.jpg";


const Main = () => {
    const photo = {
        color: 'blue',
        backgroundImage: `url(${mainPhoto})`,
    }
    return (
        <div className={style.mainBlock}>
            <div className={`${styleContainer.container} ${style.mainContainer}`}>
                <div className={style.greeting}>
                    <h2>I am Arina Kumeisha</h2>
                    <TextEffect className={style.description} text={'I like React and Redux. And I am React developer'}/>
                </div>
                <div style={photo} className={style.photo}></div>
            </div>

        </div>

    );
}

export default Main;